import React from 'react';

class Skill extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      'skills' : props.skills,
    };
  }

  formatRender(data){
    return (
      Object.keys(data).map(skill => 
        <div className="skill" key={skill}>
          <img src={require(`./${skill.toLowerCase()}.png`)} alt="js"/>
          {skill}
          <div className="skill-bar-container">
            <span className={`skill-bar level${data[skill]}`}></span>
          </div>
        </div>
      )
    );
  }

  render(){
    return (
      <div className="content">
        <div id="skill">
          <div className="column">
            { this.formatRender(this.state.skills.front) }
          </div>
          <div className="column">
            { this.formatRender(this.state.skills.back) }
          </div>
        </div>
      </div>
    );
  }
}

export default Skill;