import React from 'react';

function Formation(props){
  const formationList = props.formations.map(formation => 
    <li key={ formation.name }>
      <span className="important">{ formation.name }</span>, <span v-if="formation.mention != undefined">mention <span className="important">{ formation.mention }</span>, </span>{ formation.place }, { formation.location }&nbsp;<span className="important">({ formation.year })</span>
    </li>);

  return (
    <div className="content">
      <h2>Formation</h2>
      <ul>
        { formationList }
      </ul>
    </div>
  );
}

export default Formation;