import React from 'react';

function About(props){
  return (
    <div className="content" id="about">
      <h2>Divers</h2>
      <ul>
        {Object.keys(props.about).map((aboutKey, index) =>
          <li key={`about-${index}`}>
            <span className="important">{ aboutKey }</span> : { props.about[aboutKey].join(', ') }.
          </li>
        )}
      </ul>
    </div>
  );
}

export default About;